html {
  background-color: #fff;
  color: #000;
}
html {
  font-family: sans-serif;
  font-size: 19px;
  line-height: 1.4em;
}
.text-box {
  position: fixed;
  background: #fff;
  bottom: 2%;
  left: 2%;
  width: 96%;
  padding: 4%;
  box-sizing: border-box;
  border: 10px solid #00f;
  color: #00f;
  font-size: 3em;
  z-index: 900;
  display: none;
  border-radius: 15px;
  line-height: 1.4em;
}
.text-box-dialog {
  display: inline;
  margin-right: 1em;
}
.text-box-next {
  font-weight: bold;
  opacity: 1;
  animation: blink 0.5s infinite;
}
.blackout {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 800;
  display: none;
  background: #000;
}
h1 {
  font-size: 40px;
}
h2 {
  font-size: 30px;
}
h3 {
  font-size: 24px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.4em;
}
a,
a:active,
a:visited {
  color: inherit;
  text-decoration: none;
  transition: opacity 0.2s;
}
a:hover,
a:active:hover,
a:visited:hover {
  opacity: 0.5;
}
@-moz-keyframes blink {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-webkit-keyframes blink {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-o-keyframes blink {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes blink {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
