#stage {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.set {
  background-size: cover;
  background-position: center;
  position: fixed;
  width: 100%;
  height: 100%;
}
.bedroom-wall {
  z-index: 1;
  background-image: url("images/bedroom/wall.png");
  animation: hue-rotate 4s infinite;
}
.bedroom-furniture {
  z-index: 2;
  background-image: url("images/bedroom/furniture.png");
  -webkit-filter: blur(40px);
  filter: blur(40px);
}
.bedroom-bed {
  z-index: 3;
  background-image: url("images/bedroom/bed.png");
  -webkit-filter: blur(40px);
  filter: blur(40px);
}
.coach-angry {
  position: absolute;
  z-index: 4;
  top: 0%;
  left: 50%;
  margin-left: -300px;
  width: 600px;
  height: 100%;
  background-image: url("sprites/coach-angry.png");
  background-repeat: no-repeat;
  background-position: 0;
  background-size: 2400px;
  animation: coach-angry 0.5s steps(4, end) infinite;
  -webkit-filter: blur(40px);
  filter: blur(40px);
}
@-moz-keyframes coach-angry {
  100% {
    background-position: -2400px;
  }
}
@-webkit-keyframes coach-angry {
  100% {
    background-position: -2400px;
  }
}
@-o-keyframes coach-angry {
  100% {
    background-position: -2400px;
  }
}
@keyframes coach-angry {
  100% {
    background-position: -2400px;
  }
}
@-moz-keyframes hue-rotate {
  from {
    -webkit-filter: hue-rotate(0deg);
    filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(360deg);
    filter: hue-rotate(360deg);
  }
}
@-webkit-keyframes hue-rotate {
  from {
    -webkit-filter: hue-rotate(0deg);
    filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(360deg);
    filter: hue-rotate(360deg);
  }
}
@-o-keyframes hue-rotate {
  from {
    -webkit-filter: hue-rotate(0deg);
    filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(360deg);
    filter: hue-rotate(360deg);
  }
}
@keyframes hue-rotate {
  from {
    -webkit-filter: hue-rotate(0deg);
    filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(360deg);
    filter: hue-rotate(360deg);
  }
}
